}html, body {
  font-size: calc(14px + 1vw);
  padding: 0;
  margin: 0;
  height: 100%;
  background-image: none;
}* {
  font-family: 'Lato', sans-serif;
}html.consent {
  background-image: -webkit-gradient(linear, left top, right top, from(#332069), to(#907FBA));
  background-image: linear-gradient(90deg, #332069, #907FBA);
  background-attachment: fixed;
}a, a:visited {
  /*color: #3b4a63;*/
  color: #5A478F;
  text-decoration: none;
}p a {
  text-decoration: underline;
}h1 {
  font-size: 1.2rem;
  margin: 0;
  padding: 0;
  color: #5A478F;
}h2 {
  color: #5A478F;
  text-align: center;
  margin: 1rem 0 .5rem 0;
}h3 {
  color: #5A478F;
  margin: 0;
  margin-bottom: .25rem;
}p {
  line-height: 1.5;
  color: #1A1C29;
  margin: 0;
  margin-bottom: 1rem;
}blockquote {
  margin-left: 1rem;
  margin-right: 0;
  font-size: .8rem;
}button {
  border: none;
}section:nth-child(odd) {
  width: 100%;
  background-color: rgba(238, 238, 238, 0.3);
}.container {
  max-width: 30rem;
  margin: 0 auto;
  padding: 1.5rem;
}.image.container {
    max-width: 40rem;
    padding: 1rem 1rem 0 1rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }.image.container .image {
    -ms-flex-item-align: end;
        align-self: flex-end;
  }.image.container .image img {
    width: 15rem;
  }.image.container .text {
    margin-left: 2rem;
  }.image.container .text h2 {
    color: #31117d;
  }.image.container .text h3 {
    color: #31117d;
  }.image.container .text p {
    font-size: .9rem;
  }img {
  width: 100%;
}.join-button {
  background-color: #F5B33C;
  color: #1A1C29!important;
  border-radius: 100px;
  border: none;
  display: inline-block;
  margin: .2rem 0;
  padding: .4rem 1.6rem;
  text-decoration: none;
  font-weight: bold;
}.grayscale {
  opacity: .4;
}/* Layout of general HTML content */ol, ul {
  margin: 0;
  margin-bottom: 1rem;
  padding: 0 0 0 1.3rem;
}table {
  width: 100%;
}td {
  vertical-align: top;
}li {
  margin-top: .75rem;
  margin-bottom: .75rem;
}/* Consent as displayed in a viewer that can be maximized to view. */.maximized {
  position: absolute; 
  top: 0px; 
  left: 0px; 
  right: 0px; 
  bottom: 0px; 
  z-index: 2; 
  background-color: white;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
}.min-max-control {
  bottom: 0;
  cursor: pointer;
  position: absolute;
  right: 0;
  width: 2rem; 
  height: 2rem;
  z-index: 1;
